import { useCallback, useState } from "react"
import { Box, Button, Typography } from "@mui/material"
import { PortfolioUploadModal } from "./PortfolioUploadModal"

type Props = {
  name: string
  handleAdd: (ids: Array<string>) => void
}

const WatchlistPortfolioUpload = ({ name, handleAdd }: Props) => {
  const [modal, setModal] = useState<"json" | "ids" | null>(null)

  const onCloseModal = useCallback(() => {
    setModal(null)
  }, [])

  return (
    <>
      <Box
        sx={{
          backgroundColor: "elevation.dp01",
          mb: 1.5,
          p: 1.5,
          borderRadius: 1,
          display: "flex",
          width: "100%",
          alignItems: "center",
          justifyContent: "space-between",
        }}
      >
        <Typography sx={{ fontWeight: 700, fontSize: 18 }}>
          Portfolio Upload
          <Typography component="span" sx={{ ml: 0.5, fontSize: 18 }} color="text.secondary">
            | Upload your Mapping JSON or a list of a RavenPack IDs
          </Typography>
        </Typography>
        <Box sx={{ display: "flex" }}>
          <Button sx={{ mr: 1 }} variant="contained" onClick={() => setModal("json")}>
            JSON
          </Button>
          <Button variant="contained" onClick={() => setModal("ids")} sx={{ whiteSpace: "nowrap" }}>
            RP IDs
          </Button>
        </Box>
      </Box>
      <PortfolioUploadModal type={modal} onClose={onCloseModal} name={name} handleAdd={handleAdd} />
    </>
  )
}

export { WatchlistPortfolioUpload }
